.modal__btn{
    min-width: 160px;
    min-height: 44px;

    @include font();
    border-radius: 22px;

    color: #D41443;
    background-color: #ffffff;

    transition: background-color 250ms linear;

    &:hover,
    &:focus{
        background-color: #ECEBEB;
    }
}

.modal__btn_dark{
    color:#ffffff;
    background-color: #D41342;
}

